import { _decorator, Component, Node, Label } from 'cc';
import WebUtils from '../../../utils/WebUtils';
const { ccclass, property } = _decorator;

@ccclass('SellPlItemComponent')
export class SellPlItemComponent extends Component {
    @property(Label)
    public Label_num: Label = null;
    @property(Node)
    public Sprite_icon: Node = null;
    @property(Label)
    public Label_name: Label = null;
    private _da;
    start() {

    }

    update(deltaTime: number) {

    }

    /**
     * 
     * @param da 
     */
    public updateData(da) {
        this._da = da;
        if(this._da.seedId){
            this.Label_name.string = da.rarity+"种子";
        }else{
            this.Label_name.string = da.name;
        }
        this.Label_num.string = da.amount;
        WebUtils.getRemoteImg(this._da.icon+ "?imageView&thumbnail=162x191",this.Sprite_icon);

    }
}

